.train h2 {
  position: relative;
  margin: 0 90px 32px 0;
  font-weight: 800;
  font-size: 35px;
  line-height: 35px;
}
.train h2::before {
  content: attr(data-content);
  position: absolute;
  top: -17px;
  left: 0;
  font-weight: 800;
  font-size: 55px;
  line-height: 55px;
  color: rgba(45, 56, 63, 0.03);
}
@media (max-width: 1199px) {
  .train h2 {
    margin-bottom: 21px;
    font-size: 25px;
    line-height: 25px;
  }
  .train h2::before {
    top: -13px;
    font-size: 40px;
    line-height: 40px;
  }
}
@media (max-width: 767px) {
  .train h2 {
    margin-bottom: 16px;
    font-size: 22px;
    line-height: 22px;
  }
  .train h2::before {
    top: -4px;
    font-size: 26px;
    line-height: 26px;
  }
}

.train p {
  max-width: 820px;
  margin: 0 0 56px 0;
}
@media (max-width: 1199px) {
  .train p {
    max-width: 560px;
    margin-bottom: 66px;
  }
}
@media (max-width: 767px) {
  .train p {
    max-width: 500px;
    margin-bottom: 49px;
    font-size: 14px;
    line-height: 20px;
  }
}

.train__instructors {
  position: relative;
  width: 1200px;
  margin-bottom: 19px;
}
@media (max-width: 1199px) {
  .train__instructors {
    width: 100%;
    margin-bottom: 14px;
  }
}
@media (max-width: 767px) {
  .train__instructors {
    margin-bottom: 8px;
  }
}

.train__container {
  overflow: hidden;
}

.train__content-wrapper {
  margin: 0;
  padding: 30px 0;
  list-style-type: none;
}

.train h3 {
  margin: 0 90px 43px 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
}
@media (max-width: 1199px) {
  .train h3 {
    margin-bottom: 34px;
    font-size: 18px;
    line-height: 18px;
  }
}
@media (max-width: 767px) {
  .train h3 {
    font-size: 16px;
    line-height: 16px;
  }
}

.train__instructors h3 {
  margin: 0 90px 13px 0;
}
@media (max-width: 1199px) {
  .train__instructors h3 {
    margin-bottom: 2px;
  }
}
@media (max-width: 767px) {
  .train__instructors h3 {
    margin-bottom: 3px;
  }
}

.train__slide {
  max-width: 278px;
  height: auto;
  padding: 19px 20px 20px 20px;
  background-color: #ffffff;
  box-shadow: 0 0 30px 0 rgba(28, 51, 116, 0.1);
}
@media (max-width: 1199px) {
  .train__slide {
    max-width: none;
    padding: 15px;
  }
}
@media (max-width: 767px) {
  .train__slide {
    padding: 20px 20px 10px 20px;
  }
}

.train__image-wrapper {
  margin-bottom: 16px;
  background-color: #e8eaed;
}

.train__image-wrapper img {
  width: 100%;
}

.train__info-wrapper {
  display: grid;
  align-items: start;
  gap: 14px;
  grid-template-columns: 1fr 24px;
}
@media (max-width: 1199px) {
  .train__info-wrapper {
    gap: 12px;
  }
}
@media (max-width: 767px) {
  .train__info-wrapper {
    grid-template-columns: 1fr auto;
  }
}

.train__slide h4 {
  max-width: 145px;
  margin: 0 20px 0 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}

.train__instructor-link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 24px;
  min-height: 24px;
  margin-top: 4px;
  padding: 3px;
  background-color: #ffffff;
  box-shadow: inset 0 0 0 1px rgba(2, 102, 193, 0.1);
  transition: all 0.3s ease;
  user-select: none;
}
@media (max-width: 767px) {
  .train__instructor-link {
    margin-right: 12px;
  }
}
.train__instructor-link:hover, .train__instructor-link:focus {
  box-shadow: inset 0 0 0 1px #2d383f;
}
.train__instructor-link:active {
  background-color: #0266c1;
  box-shadow: none;
}
.train__instructor-link:active svg {
  fill: #ffffff;
}
.train__instructor-link:focus-visible {
  outline: none;
}
.train__instructor-link:disabled {
  pointer-events: none;
}
.train__instructor-link:disabled svg {
  fill: rgba(45, 56, 63, 0.3);
}

.train__instructor-link svg {
  transition: all 0.3s ease;
  fill: #2d383f;
}

.train__slide p {
  margin: 0;
  font-size: 12px;
  line-height: 16px;
  grid-column: 1/-1;
}
@media (max-width: 1199px) {
  .train__slide p {
    line-height: 18px;
  }
}

.train__buttons {
  position: absolute;
  top: -7px;
  right: 2px;
  display: flex;
  background-color: #ffffff;
}
@media (max-width: 1199px) {
  .train__buttons {
    top: -9px;
    right: 0;
  }
}
@media (max-width: 767px) {
  .train__buttons {
    top: -11px;
  }
}

.train__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 35px;
  padding: 5px;
  background-color: #0266c1;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  user-select: none;
}
.train__button:hover, .train__button:focus {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 1px #1c3374;
}
.train__button:hover svg, .train__button:focus svg {
  fill: #1c3374;
}
.train__button:active {
  background-color: #1c3374;
}
.train__button:active svg {
  fill: #ffffff;
}
.train__button:focus-visible {
  outline: none;
}
.train__button:disabled {
  background-color: rgba(2, 102, 193, 0.1);
  pointer-events: none;
}

.train__button svg {
  transition: all 0.3s ease;
  fill: #ffffff;
}

.train__button--prev {
  margin-right: 4px;
}

.train__button--next svg {
  transform: scale(-1, 1);
}

.train__learning h3 {
  margin: 0;
}
@media (max-width: 1199px) {
  .train__learning h3 {
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .train__learning h3 {
    margin: 0 0 28px 4px;
  }
}

.train__learning-list {
  display: grid;
  margin: 0;
  padding: 0 0 32px 0;
  list-style-type: none;
  gap: 30px;
  grid-template-columns: repeat(4, 1fr);
}
@media (max-width: 1199px) {
  .train__learning-list {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 91px;
  }
  .train__learning-list::before {
    display: none;
  }
}
@media (max-width: 767px) {
  .train__learning-list {
    padding: 0 26px 0 0;
    grid-template-columns: 1fr;
    row-gap: 25px;
  }
}

.train__learning-item {
  position: relative;
  display: flex;
  align-items: start;
  padding-top: 43px;
}
.train__learning-item::before {
  content: "";
  position: absolute;
  bottom: -40px;
  left: 0;
  width: 14px;
  height: 14px;
  background-color: #0266c1;
  border-radius: 50%;
}
@media (max-width: 1199px) {
  .train__learning-item {
    padding: 0 50px 0 0;
  }
  .train__learning-item::before {
    bottom: -24px;
  }
}
@media (max-width: 767px) {
  .train__learning-item {
    padding: 0 0 0 37px;
  }
  .train__learning-item::before {
    top: 13px;
    bottom: auto;
    left: 4px;
    width: 13px;
    height: 13px;
  }
  .train__learning-item::after {
    content: "";
    position: absolute;
    top: 19px;
    left: 10px;
    width: 1px;
    height: calc(100% + 25px);
    background-color: rgba(2, 102, 193, 0.2);
  }
}

@media (max-width: 1199px) {
  .train__learning-item--nature {
    padding-right: 57px;
    grid-column: 2/3;
  }
  .train__learning-item--nature::after {
    display: none;
  }
}
@media (max-width: 767px) {
  .train__learning-item--nature {
    padding-right: 0;
    grid-column: 1/-1;
  }
  .train__learning-item--nature::after {
    display: block;
  }
}
@media (max-width: 767px) {
  .train__learning-item--nature svg {
    width: 19px;
    height: 22px;
  }
}

@media (max-width: 1199px) {
  .train__learning-item--mountains {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .train__learning-item--mountains::after {
    content: "";
    position: absolute;
    top: -20px;
    left: 0;
    width: calc(100% + 30px);
    height: 1px;
    background-color: rgba(2, 102, 193, 0.2);
  }
}
@media (max-width: 767px) {
  .train__learning-item--mountains {
    grid-column: 1/-1;
    grid-row: 4/5;
  }
}

.train__learning-item:nth-child(4n+1)::after {
  content: "";
  position: absolute;
  bottom: -32px;
  left: -100vw;
  width: 200vw;
  height: 1px;
  background-color: rgba(2, 102, 193, 0.2);
}
.train__learning-item:nth-child(4n+1) svg {
  margin-top: 8px;
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+1) svg {
    margin-top: 7px;
  }
}
@media (max-width: 1199px) {
  .train__learning-item:nth-child(4n+1)::after {
    content: "";
    position: absolute;
    bottom: -17px;
    left: 0;
    width: calc(100% + 30px);
    height: 1px;
    background-color: rgba(2, 102, 193, 0.2);
  }
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+1) {
    padding-right: 0;
  }
  .train__learning-item:nth-child(4n+1)::after {
    top: 19px;
    bottom: auto;
    left: 10px;
    width: 1px;
    height: calc(100% + 25px);
  }
}

@media (max-width: 1199px) {
  .train__learning-item:nth-child(4n+2)::after {
    content: "";
    position: absolute;
    top: calc(100% + 25px);
    left: 6px;
    width: 1px;
    height: 48px;
    background-color: rgba(2, 102, 193, 0.2);
  }
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+2)::after {
    top: 19px;
    left: 10px;
    height: calc(100% + 25px);
  }
}
.train__learning-item:nth-child(4n+2) svg {
  margin-top: 9px;
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+2) svg {
    margin-top: 8px;
  }
}

@media (max-width: 1199px) {
  .train__learning-item:nth-child(4n+3)::after {
    content: "";
    position: absolute;
    top: -20px;
    left: 0;
    width: calc(100% + 36px);
    height: 1px;
    background-color: rgba(2, 102, 193, 0.2);
  }
  .train__learning-item:nth-child(4n+3)::before {
    top: -27px;
  }
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+3)::before {
    top: 13px;
  }
  .train__learning-item:nth-child(4n+3)::after {
    top: 19px;
    left: 10px;
    width: 1px;
    height: calc(100% + 25px);
  }
}
.train__learning-item:nth-child(4n+3) svg {
  margin-top: 9px;
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+3) svg {
    margin-top: 6px;
  }
}

@media (max-width: 1199px) {
  .train__learning-item:nth-child(4n+4)::before {
    top: -27px;
  }
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+4) {
    padding-right: 0;
  }
  .train__learning-item:nth-child(4n+4)::before {
    top: 13px;
  }
  .train__learning-item:nth-child(4n+4)::after {
    top: 19px;
    left: 10px;
    width: 1px;
    height: calc(100% + 25px);
  }
}
.train__learning-item:nth-child(4n+4) svg {
  margin: 8px 15px 0 1px;
}
@media (max-width: 767px) {
  .train__learning-item:nth-child(4n+4) svg {
    margin: 7px 12px 0 1px;
  }
}

@media (max-width: 767px) {
  .train__learning-item:last-child::after {
    display: none;
  }
}

.train__learning-item svg {
  flex-shrink: 0;
  margin: 0 15px 0 0;
}
@media (max-width: 1199px) {
  .train__learning-item svg {
    flex-shrink: 0;
    margin-top: 8px;
  }
}
@media (max-width: 767px) {
  .train__learning-item svg {
    margin-right: 12px;
    width: 19px;
    height: auto;
  }
}

.train__learning-item p {
  margin: 0;
}
@media (max-width: 1199px) {
  .train__learning-item p {
    font-size: 14px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .train__learning-item p {
    font-size: 12px;
    line-height: 18px;
  }
}

